import React, { Component } from "react";
import Table from "../../../6-CommonElements/2-AdvancedTable/AdvancedTable";
import Doc from "./../../../../resources/doc.svg";
import PopOver from "./../../../6-CommonElements/8-PopOver/PopOver";
import "./MyDocsTable.css";
import EditButton from "./EditDocButton";
import ViewButton from "./ViewButton";
import axios from "axios";
import serverUrl from "./../../../7-properties/1-URL";

class MyDocsTable extends Component {
  constructor(props) {
    super(props);
    this.state = { username: "", tableData: [], pagingData: {} };
  }

  columns = [
    { dataField: "id", text: "ID", sort: true },
    { dataField: "name", text: "Name", sort: true },
    { dataField: "type", text: "Type", sort: true },
    { dataField: "status", text: "Status", sort: true },
    { dataField: "date", text: "Created", sort: true },
    { dataField: "files", text: "Files", sort: false },
    { dataField: "edit", text: "", sort: false },
    { dataField: "submit", text: "", sort: false }
  ];

  sortValues = {
    id: "d.id",
    name: "d.name",
    type: "dt.name",
    status: "d.status",
    date: "d.dateCreate"
  };

  tableData = [];

  componentDidMount() {
    this.getUsername();
  }

  getUsername = () => {
    axios
      .get(serverUrl + "loggedin")
      .then(response => {
        this.setState({ username: response.data });
        this.fetchData(0, 8, null, null, "");
      })
      .catch(error => {
        console.log(error);
      });
  };

  fetchData = (page, sizePerPage, sortField, order, searchValueString) => {
    var modifiedSortField = null;
    if (sortField !== null) {
      modifiedSortField = this.sortValues[sortField];
    }

    const pageData = {
      limit: sizePerPage,
      order: order,
      page: page,
      sortBy: modifiedSortField,
      searchValueString: searchValueString
    };

    axios
      .post(serverUrl + this.state.username + "/alldocuments", pageData)
      .then(response => {
        this.setState({ pagingData: response.data.pagingData });
        this.processData(response.data.documents);
      })
      .catch(error => {
        console.log(error);
      });
  };

  processData = data => {
    const tableData = data.map((item, index) => {
      return {
        number: index,
        id: item.uid,
        name: item.name,
        type: item.type,
        status: item.status,
        date: item.dateCreate.substring(0, 10),
        files: (
          <PopOver
            popOverApparance={
              <img className="myDocsImg invert" src={Doc} alt={"..."} />
            }
            popOverTitle={"Attached:"}
            popOverContent={
              item.filesAttached.length > 0
                ? this.reduceItemsList(item.filesAttached)
                : "None"
            }
          />
        ),
        edit:
          item.status === "CREATED" ? (
            <EditButton item={item} />
          ) : (
            <ViewButton item={item} />
          ),
        uid: item.uid,
        submit: (
          <button
            className="btn btn-secondary btn-sm"
            onClick={event => {
              this.submitDocument(item.uid, event);
            }}
            disabled={this.checkDisabled(item)}
            id={item.uid}
          >
            Submit
          </button>
        )
      };
    });

    this.setState({ tableData: tableData });
  };

  submitDocument = (uid, event) => {
    event.preventDefault();
    axios
      .post(serverUrl + "doc/submit/" + uid, {})
      .then(respones => {
        window.location.reload();
      })
      .catch(error => {
        console.log(error);
      });
  };

  checkDisabled = item => {
    if (
      (item.name.length > 0) &
      (item.type.length > 0) &
      (item.description.length > 0) &
      (item.status === "CREATED") &
      (item.filesAttached.length > 0)
    ) {
      return false;
    } else {
      return true;
    }
  };

  reduceItemsList = itemsList => {
    return itemsList.reduce((sum, item, index) => {
      if (index === 0) {
        return (sum = item.fileName);
      } else {
        return (sum += ", " + item.fileName);
      }
    }, "");
  };

  fetchCustomData = url => {
    axios
      .get(serverUrl + url)
      .then(response => {
        this.processData(response.data);
      })
      .catch(error => {
        console.log(error);
      });
  };

  render() {
    return (
      <div id="myDocsTable">
        <div className="row d-flex justify-content-center ">
          <div className="btn-group btn-group-toggle" data-toggle="buttons">
            <label className="btn btn-secondary active">
              <input
                type="radio"
                name="options"
                id="all"
                checked
                onChange={() => {}}
                onClick={this.fetchData}
              />{" "}
              All
            </label>
            <label className="btn btn-secondary">
              <input
                type="radio"
                name="options"
                id="created"
                onClick={() => {
                  this.fetchCustomData("doc/allcreated/" + this.state.username);
                }}
              />{" "}
              Created
            </label>
            <label className="btn btn-secondary">
              <input
                type="radio"
                name="options"
                id="submitted"
                onClick={() => {
                  this.fetchCustomData(
                    "doc/allsubmitted/" + this.state.username
                  );
                }}
              />{" "}
              Submitted
            </label>
            <label className="btn btn-secondary">
              <input
                type="radio"
                name="options"
                id="rejected"
                onClick={() => {
                  this.fetchCustomData(
                    "doc/allrejected/" + this.state.username
                  );
                }}
              />{" "}
              Rejected
            </label>
            <label className="btn btn-secondary">
              <input
                type="radio"
                name="options"
                id="accepted"
                onClick={() => {
                  this.fetchCustomData(
                    "doc/allaccepted/" + this.state.username
                  );
                }}
              />{" "}
              Accepted
            </label>
          </div>
        </div>

        <Table
          id={"myDocsTableSearch"}
          tableData={this.state.tableData}
          searchBarId={"myDocsSearch"}
          requestNewData={this.fetchData}
          pagingData={this.state.pagingData}
          columns={this.columns}
          selectType={"checkbox"}
          handleRowSelect={() => {}}
          setSelectedItems={() => {}}
        />
      </div>
    );
  }
}

export default MyDocsTable;
